export default {
	data() {
		return {}
	},
	methods: {
		add() {
			uni.showLoading({
				title: '处理中...'
			})
			uniCloud.callFunction({
				name: 'add',
				data: {
					name: 'DCloud',
					subType: 'uniCloud',
					createTime: Date.now()
				}
			}).then((res) => {
				uni.hideLoading()
				uni.showModal({
					content: `成功添加一条数据，文档id为：${res.result.id}`,
					showCancel: false
				})
				console.log(res)
			}).catch((err) => {
				uni.hideLoading()
				uni.showModal({
					content: `添加数据失败，错误信息为：${err.message}`,
					showCancel: false
				})
				console.error(err)
			})
		},
		remove() {
			uni.showLoading({
				title: '处理中...'
			})
			uniCloud.callFunction({
				name: 'remove'
			}).then((res) => {
				uni.hideLoading()
				uni.showModal({
					content: res.result.msg,
					showCancel: false
				})
				console.log(res)
			}).catch((err) => {
				uni.hideLoading()
				uni.showModal({
					content: `删除失败，错误信息为：${err.message}`,
					showCancel: false
				})
				console.error(err)
			})
		},
		update() {
			uni.showLoading({
				title: '处理中...'
			})
			uniCloud.callFunction({
				name: 'update',
				data: {
					name: 'DCloud',
					subType: 'html 5+',
					createTime: Date.now()
				}
			}).then((res) => {
				uni.hideLoading()
				uni.showModal({
					content: res.result.msg,
					showCancel: false
				})
				console.log(res)
			}).catch((err) => {
				uni.hideLoading()
				uni.showModal({
					content: `更新操作执行失败，错误信息为：${err.message}`,
					showCancel: false
				})
				console.error(err)
			})
		},
		get() {
			uni.showLoading({
				title: '处理中...'
			})
			uniCloud.callFunction({
				name: 'get'
			}).then((res) => {
				uni.hideLoading()
				uni.showModal({
					content: `查询成功，获取数据列表为：${JSON.stringify(res.result.data)}`,
					showCancel: false
				})
				console.log(res)
			}).catch((err) => {
				uni.hideLoading()
				uni.showModal({
					content: `查询失败，错误信息为：${err.message}`,
					showCancel: false
				})
				console.error(err)
			})
		},
		useCommon() {
			console.log('请确保自己已经阅读并按照公用模块文档操作 https://uniapp.dcloud.io/uniCloud/cf-common')
			uniCloud.callFunction({
				name: 'useCommon'
			}).then((res) => {
				uni.hideLoading()
				uni.showModal({
					content: '云函数useCommon返回结果：' + JSON.stringify(res.result),
					showCancel: false
				})
				console.log(res)
			}).catch((err) => {
				uni.hideLoading()
				uni.showModal({
					content: `云函数useCommon执行失败，错误信息为：${err.message}`,
					showCancel: false
				})
				console.error(err)
			})
		},
		upload() {
			new Promise((resolve, reject) => {
				uni.chooseImage({
					chooseImage: 1,
					success: res => {
						const path = res.tempFilePaths[0]
						let ext




						ext = res.tempFilePaths[0].split('.').pop()

						const options = {
							filePath: path,
							cloudPath: Date.now() + '.' + ext
						}
						resolve(options)
					},
					fail: () => {
						reject(new Error('Fail_Cancel'))
					}
				})
			}).then((options) => {
				uni.showLoading({
					title: '文件上传中...'
				})
				return uniCloud.uploadFile({
					...options,
					onUploadProgress(e) {
						console.log(e)
					}
				})
			}).then(res => {
				uni.hideLoading()
				console.log(res);
				uni.showModal({
					content: '图片上传成功，fileId为：' + res.fileID,
					showCancel: false
				})
			}).catch((err) => {
				uni.hideLoading()
				console.log(err);
				if (err.message !== 'Fail_Cancel') {
					uni.showModal({
						content: `图片上传失败，错误信息为：${err.message}`,
						showCancel: false
					})
				}
			})
		}
	}
}
